import t from "../../common/time.js"
export default {
	name: "user-space-userinfo",
	props: {
		userinfo: Object
	},
	computed:{
		getRegAge(){
			return t.gettime.sumAge(this.userinfo.regtime);
		},
		getXingZuo(){
			return this.userinfo.birthday ? t.gettime.getHoroscope(this.userinfo.birthday) : '未知';
		}
	}
}
